<script>
    export let project;

    let yearEnd = project.yearEnd == null ? new Date().getFullYear() : '';
    let yearSep = project.yearStart !== project.yearEnd ? '-' : '';

    let projectContainer;

    function showGallery() {
      lightGallery(projectContainer, {
                  dynamic: true,
                  hideControlOnEnd: true,
                  preload: 2,
                  download: false,
                  dynamicEl: project.images,
                  mode: 'lg-slide'
              });
    }

</script>

<style>
abbr {
    text-decoration: none;
    cursor: help;
    background: rgba(216, 49, 49, 0.05);
    padding: 0 4px;
    border-radius: 4px;
}

.project-description-text {
    text-align: center;
    text-align-last: center;
    max-width: 90%;
    margin: auto;
}


.card {
    box-shadow: 0 -4px 2px hsla(0, 0%, 0%, 0);
    border: 1px solid #e6e6e6;
    transition: 0.2s border, 0.4s box-shadow;
    border-radius: 4px;
    min-height: 21rem;
    cursor: pointer;
}

.card:hover {
    border: 1px solid white;
    box-shadow: 0 0 6px hsla(0, 0%, 0%, 0.1), 0 6px 24px hsla(0, 0%, 0%, 0.1);
}

.card .card-content {
    height: 100%;
    display: flex;
}

.card .container {
    margin: auto;
}

.project-image {
    min-height: 4rem;
    height: 5rem;
    width: 100%;
    padding: 0 5% 0.5rem;
    margin: 0.5rem auto;
    object-fit: contain;
    position: center;
    /*filter: saturate(0);*/
    /*transition: 0.4s filter;*/
}
</style>



<div class="column is-narrow is-12-mobile is-4-tablet is-4-desktop is-3-widescreen" on:click={showGallery} bind:this={projectContainer}>
    <div class="card">
        <div class="card-content">
           <div class="container has-text-centered">
                <img src="{project.heroImage}" alt="{project.name}">

                <h4 class="title is-5">{@html project.name}</h4>
                <h6 class="subtitle is-6">{project.yearStart} {yearSep} {yearEnd}</h6>

                <p class="is-6 has-text-gray-lighter project-description-text">
                    {@html project.description}
                </p>
           </div>
        </div>

    </div>
</div>