<script>
    export let skill;
</script>

<style>
    .mb-6 {
       margin-bottom: 0.6rem !important;
    }
</style>

<div class="columns is-variable is-5" style="margin-bottom: 2rem;">

<div class="column is-12-mobile has-text-centered-mobile is-4-tablet is-3">

    <h4 class="subtitle is-4 skill-name">
        <span class="icon {skill.iconColor}"><i class="{skill.icon}"></i></span>
        <span>{skill.name}</span>
    </h4>

</div>

<div class="column is-9 columns is-multiline">

    {#each skill.items as item}

        <div class="column is-12-mobile has-text-centered-mobile is-5-tablet is-4-desktop">
            <ul>
                {#if item.section != null}
                    <li class="mb-6"><b class="{skill.iconColor} is-size-5">{item.section}</b></li>

                    {#each item.items as itm}
                        <li class="mb-6">

                            <b class="is-size-6">{@html itm.name}</b>

                            {#if itm.subtitle != null}
                            <br>
                            <span class="is-size-6">{@html itm.subtitle}</span>
                            {/if}

                            <br>
                            <span class="has-text-grey-light is-size-6">{@html itm.level}</span>

                        </li>
                    {/each}
                {:else}

                <li class="mb-6">

                    <b class="is-size-6">{@html item.name}

                    {#if item.subtitle != null}
                    <br>
                    <span class="is-size-6">{@html item.subtitle}</span>
                    {/if}

                    <br>
                    <span class="has-text-grey-light is-size-6">{@html item.level}</span>

                    </b>

                </li>

                {/if}
            </ul>
        </div>

    {/each}

</div>

</div>
